.card {
  background: #fff;
  margin-bottom: 10px; }
  .card :global(.card) {
    box-shadow: none; }
  .card .breakLine {
    width: calc(100% - 20px);
    text-align: center;
    margin: 0 auto; }
  .card .time {
    font-size: 13px;
    padding-top: 10px;
    color: #888;
    letter-spacing: 0.04em; }
    .card .time :global(.icon) {
      font-size: 18px;
      vertical-align: middle; }
  .card .totalContainer {
    padding-left: 12.5px;
    padding-top: 13px; }

.price {
  font-size: 15px;
  font-weight: 500;
  color: #000;
  width: auto;
  display: inline-block;
  text-align: right; }

.label {
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  display: inline-block;
  width: calc(100% - 100px); }

.serviceCharge, .totalCharge {
  display: block;
  width: 100%;
  padding: 2px 0 2px 0;
  font-size: 13px;
  color: #888888; }
  .serviceCharge .price, .totalCharge .price {
    font-size: 15px;
    font-weight: 400;
    color: #000;
    min-width: 100px;
    max-width: 100%;
    width: auto;
    word-break: break-all;
    display: inline-block;
    text-align: right; }
  .serviceCharge .label, .totalCharge .label {
    font-weight: 300; }

.totalCharge {
  font-weight: 900;
  font-size: 15px;
  color: #000; }
  .totalCharge .price {
    font-weight: 900; }
  .totalCharge .label {
    font-size: 15px;
    font-weight: 900; }



