@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900");
html {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  letter-spacing: 0.01em;
  padding-bottom: 10px;
  background: #f4f4f4;
  color: #000000;
  font-size: 13px;
  font-weight: 300;
  width: 100%;
  padding-top: 55px;
  overflow: scroll;
  height: 100%;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }

* {
  -webkit-overflow-scrolling: touch; }

:focus {
  outline: none !important; }

* {
  font-family: Roboto, sans-serif; }

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: normal; }

span {
  font-family: Roboto, sans-serif; }

h1 {
  font-size: 32px;
  line-height: 1.2;
  font-weight: normal; }

h2 {
  font-size: 28px;
  line-height: 1.2;
  font-weight: 300; }

hr {
  display: block;
  width: 100%;
  box-sizing: border-box;
  height: 1px;
  border: none;
  background: #e6e6e6;
  margin: 0;
  padding: 0; }

p {
  font-size: 13px;
  font-weight: 300;
  position: relative; }

button {
  border: none;
  cursor: pointer;
  -webkit-appearance: none;
  position: relative;
  overflow: hidden; }

*:focus {
  outline: none; }

div, header {
  box-sizing: border-box;
  position: relative; }

span {
  display: inline-block;
  position: relative;
  box-sizing: border-box; }

header {
  background: #000f23;
  width: 100%;
  color: #fff;
  height: 56px;
  display: block;
  padding: 18px 13px 18px 20px;
  vertical-align: top;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2; }

footer {
  font-size: 0;
  letter-spacing: 0;
  background: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
  z-index: 2;
  -webkit-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1); }
  footer :global(.blue), footer :global(.orange) {
    -webkit-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
    text-transform: uppercase; }

input, textarea {
  color: #000;
  font-weight: 300;
  border: none;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 20px; }
  input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #ddd; }

:global(.card) {
  display: block;
  border-radius: 4px;
  overflow: hidden;
  -webkit-box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.4);
  -moz-box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.4);
  box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.4);
  width: 100%; }

:global(.container) {
  padding: 10px;
  width: 100%; }

:global(.btn-half-wrapper) {
  width: 50%;
  display: inline-block;
  padding: 10px; }

:global(.btn) {
  padding: 18px;
  font-size: 14px;
  text-align: center;
  width: 100%;
  border-radius: 4px;
  background: transparent;
  color: #000f23;
  vertical-align: middle;
  font-weight: 500;
  letter-spacing: 0.05em;
  cursor: pointer; }
  :global(.btn):global(.blue) {
    color: #fff;
    background: #000f23; }
  :global(.btn):global(.orange) {
    color: #fff;
    background: #ff8400; }
  :global(.btn):global(.small) {
    text-transform: none;
    padding: 6px 11px;
    width: auto;
    border-radius: 30px;
    font-weight: 300; }
    :global(.btn):global(.small):global(.blue) {
      color: #fff;
      background: #1A2739; }
  :global(.btn):disabled {
    opacity: 0.5; }
  :global(.btn):active {
    opacity: 0.5; }

:global(#root) {
  height: 100%;
  box-sizing: border-box; }

:global(.hide) {
  display: none !important; }

:global(.picker-scroller) {
  -webkit-transition: all .3s;
  /* Safari */
  -webkit-overflow-scrolling: touch; }



