.fieldName {
  font-size: 13px;
  color: #a89e8a;
  vertical-align: top;
  width: 105px;
  font-weight: 400;
  display: inline-block;
  font-size: 13px;
  overflow: hidden;
  padding: 20px 10px 20px 12px;
  line-height: 20px;
  vertical-align: middle; }

.fieldInput {
  vertical-align: top;
  width: calc(100% - 107px);
  display: inline-block;
  padding: 20px 10px;
  font-size: 15px;
  font-weight: 300;
  line-height: 20px;
  text-align: left;
  padding-right: 40px;
  background: #fff;
  position: relative;
  vertical-align: middle; }
  .fieldInput input, .fieldInput textarea {
    width: 100%; }
  .fieldInput .remarksInput {
    resize: none; }
  .fieldInput::after {
    content: "\34";
    font-family: handy;
    position: absolute;
    top: 50%;
    right: 21px;
    transform: translateY(-50%);
    font-size: 13px;
    height: 19px;
    color: #888888; }

.red {
  color: #d0021b; }

.error {
  display: block;
  font-size: 11px;
  color: #d0021b;
  font-weight: 300;
  line-height: 12px;
  margin-top: 4px; }



