.title {
  padding: 20px; }

.scrollable {
  max-height: 500px;
  overflow: scroll; }
  .scrollable :global(.btn) {
    display: inline;
    width: 50%;
    color: #888888;
    text-transform: uppercase; }
  .scrollable :global(.btn.active) {
    border-bottom: 2.5px solid #000f23;
    border-radius: 0;
    color: #000f23; }
  .scrollable :global(.picker-item) {
    font-size: 15px;
    letter-spacing: 1px;
    font-weight: 300; }
    .scrollable :global(.picker-item):global(.picker-item-selected) {
      font-weight: 400; }
  .scrollable :global(.picker-container .picker-highlight):before, .scrollable :global(.picker-container .picker-highlight):after {
    height: 2px;
    transform: none;
    background-color: #000f23; }

.bottomControls {
  -webkit-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1); }
  .bottomControls :global(.btn):active {
    opacity: 0.5; }

.button {
  text-transform: uppercase; }



