.incrementer button {
  text-align: center;
  border: 1px solid #ddd;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 20px;
  background: #fbfbfb;
  display: inline-block;
  vertical-align: middle;
  padding: 0; }
  .incrementer button:active {
    background-color: #ddd; }
  .incrementer button.disabled {
    border-color: #f1f1f1; }
    .incrementer button.disabled :global(.icon) {
      color: #bbb; }
    .incrementer button.disabled:active {
      background: #fbfbfb; }

.incrementer .count {
  width: 50px;
  padding: 0 3px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  font-weight: 500;
  font-size: 15px;
  color: #000; }

.incrementer :global(.icon) {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
  width: 14px;
  font-size: 14px;
  height: 14px;
  line-height: 10px;
  vertical-align: middle;
  color: #000f23; }



