:global(.card).card {
  padding: 0 12px;
  background: #fff;
  margin: 10px 0; }
  :global(.card).card:global(.error) {
    border: 1px solid #d0021b; }

.error {
  display: block;
  font-size: 11px;
  text-align: left;
  color: #d0021b;
  padding-bottom: 1px; }

.heading {
  padding: 20px 0 5px 0; }

.categoryName {
  font-size: 13px;
  color: #a89e8a;
  font-weight: 400;
  width: 60%; }

.options {
  float: right;
  color: #888888;
  font-size: 13px;
  font-weight: 300;
  padding-bottom: 5px; }



