.name {
  font-size: 15px;
  line-height: 17px;
  font-weight: 300;
  min-width: calc(60%);
  padding: 10px 0;
  display: inline-block;
  vertical-align: middle; }

.left {
  width: calc(100% - 110px);
  display: inline-block;
  vertical-align: middle;
  padding-right: 10px; }

.price {
  font-size: 13px;
  font-weight: 600;
  opacity: 0.2;
  line-height: 17px;
  word-break: break-all;
  min-width: calc(40%);
  display: inline-block;
  vertical-align: middle;
  padding: 5px 0; }

.choice {
  display: block;
  width: 100%;
  font-size: 0;
  min-height: 60px; }
  .choice.active .name {
    font-weight: 500; }
  .choice.active .price {
    opacity: 1; }

.controls {
  text-align: right;
  padding: 18px 0;
  width: 110px;
  display: inline-block;
  vertical-align: middle; }



