.hero {
  margin-bottom: 5px; }
  .hero .backgroundColor {
    content: "";
    display: block;
    width: 100%;
    height: 84%;
    top: 0;
    left: 0;
    position: absolute;
    background: #000f23; }

.heroImgBackground {
  height: calc(100vh - 170px);
  max-height: 543px;
  min-height: 380px;
  border-radius: 4px;
  background-color: #fff;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(""); }
  .heroImgBackground::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 4px;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,000f23+100&0+0,0.66+100 */
    background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(0, 15, 35, 0.66) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(0, 15, 35, 0.66) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(0, 15, 35, 0.66) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#99000f23',GradientType=0 );
    /* IE6-9 */ }

.circleArrow {
  background: #ff8400;
  border-radius: 40px;
  width: 65px;
  height: 65px;
  position: absolute;
  right: 21px;
  bottom: -32px;
  padding: 0;
  -webkit-box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.4);
  -moz-box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.4);
  box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.4); }
  .circleArrow :global(.icon) {
    color: #fff;
    font-size: 14px;
    height: 14px;
    margin-top: 5px; }
  .circleArrow:active {
    background: #e27400;
    -webkit-box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.7);
    -moz-box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.7);
    box-shadow: 0px 1px 1px 1px rgba(148, 148, 148, 0.7); }

.overlayText {
  color: #fff;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px 70px 20px 20px; }
  .overlayText h1 {
    padding: 20px 0; }
  .overlayText p {
    padding: 6px 0;
    display: block; }



