.checkbox {
  text-align: center;
  border: 2px solid #000f23;
  width: 30px;
  height: 30px;
  display: inline-block;
  background: transparent;
  position: relative; }
  .checkbox :global(.icon) {
    font-size: 14px;
    color: #000f23;
    position: absolute;
    top: 6px;
    left: 6px; }
  .checkbox.disabled {
    border-color: #eee; }



