.datetimehr {
  margin-top: 25px;
  margin-bottom: 10px;
  display: block;
  width: 100%;
  text-align: center;
  position: relative; }
  .datetimehr span {
    font-size: 13px;
    color: #888888;
    text-align: center;
    display: inline-block;
    padding: 0 5px;
    background: #f4f4f4; }
  .datetimehr::before {
    content: "";
    position: absolute;
    top: 7px;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background: #888888; }



